import React from 'react';

const PrincipalCard = () => {
  return (
    <section className="bg-gradient-to-b from-white to-blue-50 py-16">
      <div className="max-w-4xl mx-auto px-4">
        <div className="bg-white rounded-2xl shadow-xl p-8 md:flex md:items-center gap-8">
          
          {/* Principal Photo */}
          <div className="flex-shrink-0 mb-6 md:mb-0">
            <img
              src="/images/principal.jpg" // Replace with actual path
              alt="Principal"
              className="w-40 h-40 rounded-full object-cover border-4 border-blue-500 shadow-md mx-auto"
            />
          </div>

          {/* Info */}
          <div>
            <h2 className="text-3xl font-bold text-blue-900 mb-2">Dr. Priya Sharma</h2>
            <p className="text-blue-700 font-medium mb-4">Principal, Sunrise Public School</p>
            <p className="text-gray-700 leading-relaxed">
              With over 20 years of experience in educational leadership, Dr. Priya Sharma is committed to nurturing young minds and fostering an environment of academic excellence and holistic development. Her vision is to empower students to become responsible global citizens with a strong moral foundation.
            </p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default PrincipalCard;
